import { DateTime } from "luxon";
import * as yup from "yup";

const today = DateTime.now();
const eighteenYearsAgo = today.minus({ years: 18 }); //  18  
const oneHundredYearsAgo = today.minus({ years: 100 }); //  100  

export const userShapeObject = {
  firstName: yup
    .string()
    .required(" ")
    .min(3, "     3 ")
    .max(100, "     100  ")
    .matches(/^[A-Za-z--]+$/, "    "),
  lastName: yup
    .string()
    .min(3, "     3 ")
    .max(100, "     100  ")
    .matches(/^[A-Za-z--]+$/, "    "),
  birthday: yup
    .date()
    .nullable()
    .notRequired()
    .test("age-check", "     18 ", (value) => {
      if (value) {
        return value <= eighteenYearsAgo;
      }
      return true; 
    })
    .test("age-check-max", "     100 ", (value) => {
      if (value) {
        return value >= oneHundredYearsAgo;
      }
      return true;
    }),
};
