import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { CITIES, TRIP_STATUSES } from "../../../constants";

const shapeTrip = {
  fromWhere: yup
    .string()
    .required("   ")
    .notOneOf([CITIES[0]], "   "),
  toWhere: yup
    .string()
    .required("   ")
    .notOneOf([CITIES[0], yup.ref("fromWhere")], "   "),

  passengerPrice: yup.number().required("  ").moreThan(0, "    0"),

  numberPeople: yup
    .number()
    .required(" - ")
    .moreThan(0, "      1 "),
  status: yup.string(),
};

const tripFormSchema = yup.object().shape(shapeTrip);

export const tripFormParams = {
  defaultValues: {
    fromWhere: CITIES[0],
    toWhere: CITIES[0],
    passengerPrice: 0,
    createdBy: null,
    status: TRIP_STATUSES.NEW.text,
    numberPeople: 1,
    totalPrice: 0,
    userId: null,
  },
  resolver: yupResolver(tripFormSchema),
};
