import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
const registerShapeObject = {
  email: yup
    .string()
    .required(" email")
    .matches(/\w+$/, " email.")
    .min(3, "Email     3 ")
    .max(64, "Email  . ,  email,   64 "),
  firstName: yup
    .string()
    .required(" ")
    .min(3, "     3 ")
    .max(100, "     100  ")
    .matches(/^[A-Za-z--]+$/, '    '),
  lastName: yup
    .string()
    .min(3, "     3 ")
    .max(100, "     100  ")
    .matches(/^[A-Za-z--]+$/, '    '),
  password: yup
    .string()
    .required(" ")
    .matches(/^[\w#$]+$/, " .     ,    #%")
    .min(6, "     6 ")
    .max(60, "  . ,      60 "),
  repeatPassword: yup
    .string()
    .required(" ")
    .oneOf([yup.ref("password"), null], "  "),
};

const loginShapeObject = {
  email: yup.string().required(" email"),
  password: yup.string().required(" "),
};

export const getAuthFormParams = (isRegister) => {
  const authFormSchema = yup.object().shape(isRegister ? registerShapeObject : loginShapeObject);
  return isRegister
    ? {
        defaultValues: {
          login: "",
          email: "",
          password: "",
          repeatPassword: "",
          isDriver: false,
        },
        resolver: yupResolver(authFormSchema),
      }
    : {
        defaultValues: {
          email: "",
          password: "",
        },
        resolver: yupResolver(authFormSchema),
      };
};
